<template>
    <div class="canvaswrapper">

      <DesmosExpressionsComponent
        ref="desmosExpressions"
      ></DesmosExpressionsComponent>

      <div class="wrapper is--search">
        <!-- <uflo-select-with-create2
            v-on:selected="onSelectOption"
            v-on:update-option="(value) => {desmosUrls = value.filter((a) => isValidHttpUrl(a.name))}"
            placeholder="Select or Insert DesmosId"
            :selectedOption="desmosUrls[0] ? desmosUrls[0]: {}"
            :options="desmosUrls"
        >
        </uflo-select-with-create2> -->
        <div class="search is--template-border w-form">
          <input
            class="search_input w-input"
            autofocus
            v-model="desmosUrl"
            placeholder="Desmos URL"
            dark
          />
        </div>
        <a
          class="icon-btn is--template-border"
          @click="setFirstState()"
        >
          <img
            src="../images/refresh-button.svg"
            loading="lazy"
            alt=""
            class="icon-img is--large"
          />
        </a>
      </div>

      <div
        @click="onClickToDesmos"
        @keyup="$emit('on-click-to-desmos')" 
        class="desmos_embed w-embed w-script"
        id="calculator"
      >
      </div>
      <!-- <div
        v-for="(desmos, index) in desmosUrls"
        :key="index"
        v-show="desmos.name == desmosUrl"
        @click="onClickToDesmos"
        @keyup="$emit('on-click-to-desmos')" 
        class="desmos_embed w-embed w-script"
        :id="`calculator${desmos.id}`"
      >
      </div> -->

      <div class="canvas-inst large-blur">
        <div>Select the expression in which you want to add tangent for.</div>
      </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    desmosUrl: '',
    desmosUrls: [],
    desmosId: 0,
  }),
  props: {
    stateList: {
      type: String
    }
  },
  components: {
    DesmosExpressionsComponent: () => import('../components/DesmosExpressionsComponent.vue'),
    // UfloSelectWithCreate2: () => import('../components/uflo-select-with-create2.vue'),
  },
  mounted () {
    const canvasID = this.$route.query.canvasID;
    // this.desmosId = window.desmosId;
      const Desmos = window.Desmos;
      const myColors = {
        green: '#ff0057',
        blue: '#ff1e00',
        red: '#00d58e',
        pink: '#00ff00',
        yellow: '#0011ff',
        purple: '#aaff00',
        guideline: 'rgba(0, 0, 0, 0.25)',
        white: '#000000'
      }

      const elt = document.getElementById('calculator')

      window.calculator = Desmos.GraphingCalculator(elt, {
        colors: myColors,
        border: false,
        invertedColors: true,
        showGrid: false,
        projectorMode: true,
        keypad: true,
        expressionsCollapsed: true,
        settingsMenu: true,
        lockViewport: false,
        fontSize: 16,
        expressions: true,
      })
    if (canvasID) {
      // this.desmosUrls = canvasID.split(',').map((a, idx) => {return {id: idx, name: a}});
      this.desmosUrl = canvasID;
    }
    // if (canvasID) {
    //   this.desmosUrls = canvasID.split(',').map((a, idx) => {return {id: idx, name: a}});
    //   this.desmosUrl = canvasID.split(',')[0];
    // }
    // this.onSetDesmos();
  },
  methods: {
    onClickToDesmos (e) {
      // to select expression for dropdown component id list in create code
      this.$emit('on-click-to-desmos', e);
      // to select in desmos expressions component
      this.$refs.desmosExpressions.onSelectExpressionFromDesmos()
    },
    async setFirstState () {
      const loader = this.$loading.show();
      window.desmosUrl = this.desmosUrl;
      const url = new URL(this.desmosUrl);
      const response = await this.$axios({ url: url.pathname, baseURL: url.origin }, {
        headers: {
          accept: 'application/json',
        }
      });
      const firstState = response.data.state;
      window.calculator.setState(firstState);
      // window[`calculator${desmosId}`].setState(firstState);
      loader.hide()
    },
    // onSelectOption (value) {
    //   this.desmosUrl = value.name;
    //   if (value && value.name && this.desmosUrl !== value.name) {
    //     if (this.isValidHttpUrl(this.desmosUrl)) {
    //       this.onSetDesmos(value.id);
    //       this.setFirstState(value.id);
    //     } else {
    //       alert("Please insert a valid url!")
    //     }
    //   }
    // },
    onSetDesmos (id) {
      window.desmosId = id ? id: 0;
      this.desmosId = window.desmosId;
      const Desmos = window.Desmos;
      const myColors = {
        green: '#ff0057',
        blue: '#ff1e00',
        red: '#00d58e',
        pink: '#00ff00',
        yellow: '#0011ff',
        purple: '#aaff00',
        guideline: 'rgba(0, 0, 0, 0.25)',
        white: '#000000'
      }

      const elt = document.getElementById('calculator' + window.desmosId)

      window[`calculator${window.desmosId}`] = Desmos.GraphingCalculator(elt, {
        colors: myColors,
        border: true,
        invertedColors: true,
        showGrid: true,
        projectorMode: false,
        keypad: false,
        expressionsCollapsed: true,
        settingsMenu: false,
        lockViewport: false,
        fontSize: 16,
        expressions: true
      })
    },
    isValidHttpUrl(string) {
      if (!string) return true
      let url;
      try {
        url = new URL(string);
      } catch (_) {
        return false;  
      }
      if (url) {
        return true;
      }
    }
  }
}
</script>
<style>

</style>
