// import store from '../store'
// const Store = store();

function setTopIndex (name, frameIdStartOption) {
    for (let [index, item] of frameIdStartOption.entries()) {
        if (item === name) {
            return index
        }
    }
}

function exacName(name) {
    if (name === "Color") {
        return "color";
    } else if (
        name === "SYNC" ||
        name === "SYNC-Backward" ||
        name === "SYNC-Constant" ||
        name === "SYNC-Custom"
    ) {
        return "latex";
    } else if (name === "Hidden") {
        return "hidden";
    } else if (name === "Select") {
        return "select";
    }
}


const engine = (stateList, frameIdStartOption, firstState, colors, calculatorId) => {

    const code = [];

    // run in executing code only
    code.push(`
        var state = ${JSON.stringify(firstState)}
        window.calculator.setState(state)
    `);

    code.push(`function getScrollTop() {
        return window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0; \n
      }`);

    for (const [index, item] of frameIdStartOption.entries()) {
        code.push(`
            var frameTop${index} = document.getElementById('${item}').offsetTop + 200 \n
            var frameBottom${index} = frameTop${index} + window.screen.height - 200 \n
        `);
    }

    code.push(`
        window.checkIfInView = function () { \n
            if (!window.locked) {
            var viewTop = getScrollTop(); \n
            var viewBottom = viewTop + window.screen.height;
            window.activeStateId = null;
            var activeStateIndex = -1;
            var changingExpIds = [];
    `);

    for (const [index, item] of stateList.entries()) {

        // do state functions
        if (item && item.type === 'inView') {
            code.push(`if (viewBottom > frameTop${setTopIndex(item.startFrame, frameIdStartOption) } && frameBottom${setTopIndex(item.startFrame, frameIdStartOption) } > viewBottom) {`);
                
            // set active state global variable to detect active state in execute only
            code.push(`
                window.activeStateId = '${item.startFrame}';
                activeStateIndex = ${index};
                changingExpIds = [${stateList[index].transformation.map((t) => {
                    if (/^\d+$/.test(t.compId)) {
                        return t.compId;
                    }
                })}];
                `);

            code.push(`var progress = (viewBottom - frameTop${setTopIndex(item.startFrame, frameIdStartOption) }) /
            (frameBottom${setTopIndex(item.startFrame, frameIdStartOption) } - frameTop${setTopIndex(item.startFrame, frameIdStartOption) });`)

            for (const compItem of stateList[index].transformation) {

                if ((compItem.attribute === 'SYNC' || compItem.attribute === 'SYNC-Backward') && compItem.compId !== '') {

                    const itemExp = firstState.expressions.list.find((exp) => exp.id === compItem.compId);
                    let { min, max, step } = itemExp.sliderBounds ? itemExp.sliderBounds : itemExp.slider || { min: 0, max: 15, step: 1 };

                    if (compItem.attribute === 'SYNC-Backward') {
                        code.push('progress = Math.abs(1 - progress)');
                    }
 
                    if (!/^\d+$/.test(min) || !/^\d+$/.test(max)) {
                        max ? max = max.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';
                        min ? min = min.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';

                        code.push(`
                        window.calculator.setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${itemExp.latex.split('=')[0]}= ${min == 0 ? "": min} + " + progress + "( ${max ? max : 15} - ${min ? min : 0})",
                            sliderBounds: { min: "${min ? min : 0}", max: "${max ? max : 15}", step: ${step ? step: 1} }
                        })`);
                    } else {

                        code.push(`window.calculator.setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${itemExp.latex.split('=')[0]}= " + ${min == 0 ? "": min} + progress * (${max ? +max : 15} - ${min ? +min : 0}),
                            sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: 1} }
                        })`);

                    }
                }

                if (compItem.attribute === 'SYNC-Custom' && compItem.compId !== '') {

                    let { min, max, step } = compItem.sliderBounds ? compItem.sliderBounds : compItem.slider;

                    if (!/^\d+$/.test(min) || !/^\d+$/.test(max)) {
                        max ? max = max.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';
                        min ? min = min.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';

                        code.push(`
                        window.calculator.setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${compItem.latex.split('=')[0]}= ${min == 0 ? '': min} + " + progress + "( ${max ? max : 15} - ${min ? min : 0})",
                            sliderBounds: { min: "${min ? min : 0}", max: "${max ? max : 15}", step: ${step ? step: 1} }
                        })`);
                    } else {

                        code.push(`window.calculator.setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${compItem.latex.split("=")[0]}= " + ${min == 0 ? '': min} + progress * (${max ? +max : 15} - ${min ? +min : 0}),
                            sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: 1} }
                        })`);
                    }

                }

                if (compItem.attribute === 'SYNC-Constant' && compItem.compId !== '') {

                    let { min, max, step } = compItem.sliderBounds ? compItem.sliderBounds : compItem.slider;

                    code.push(`window.calculator.setExpression({
                        id:"${compItem.compId }",
                        ${exacName(compItem.attribute) }: "${compItem.latex}",
                        sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: 1} }
                    })`);

                }

                if (compItem.attribute === 'Hidden' && compItem.compId !== '') {
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: ${compItem.value}})`);
                }

                if (compItem.attribute === 'Select' && compItem.compId !== '') {
                    if (compItem.value === 'true') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 1, lineWidth: 6})`);
                    } else if (compItem.value === 'false') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 0.5, lineWidth: 2.5})`);
                    } else if (compItem.value === 'default') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 0.9, lineWidth: 2.5})`);
                    }
                }

                if (compItem.attribute === 'Color' && compItem.compId !== '') {
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: window.calculator.colors.${compItem.value}})`);
                }

                if (compItem.attribute === 'Custom' && compItem.compId !== '') {
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${compItem.customAttr }: ${compItem.customTrans }})`);
                }

                if (compItem.attribute === 'showLabel' && compItem.compId !== '') {
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${compItem.attribute}: ${compItem.value }})`);
                }

                if (compItem.attribute === 'pointActive' && compItem.compId !== '') {
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", showLabel: ${compItem.value }})`);
                    code.push(`window.calculator.setExpression({id:"${compItem.compId }", hidden: ${compItem.value === 'true' ? 'false': 'true'}})`);
                }

                if (compItem.attribute === 'Math Boundary') {
                    code.push(`window.calculator.setMathBounds({
                        left: ${+compItem.left},
                        right: ${+compItem.right},
                        bottom: ${+compItem.bottom},
                        top: ${+compItem.top}
                    })`);
                }

                if (compItem.attribute === 'xAxisLabel') {
                    code.push(`window.calculator.updateSettings({
                        xAxisLabel: "${compItem.xAxisLabel}",
                    })`);
                }

                if (compItem.attribute === 'yAxisLabel') {
                    code.push(`window.calculator.updateSettings({
                        yAxisLabel: "${compItem.yAxisLabel}",
                    })`);
                }
            }

            code.push('}')
        }

        if (item && item.type === 'enter') {
            code.push(`if (viewBottom > frameTop${setTopIndex(item.startFrame, frameIdStartOption) } && frameBottom${setTopIndex(item.startFrame, frameIdStartOption) } > viewTop) {`);

            // set active state global variable to detext active state in execute only 
            code.push(`
                window.activeStateId = '${item.startFrame}';
                activeStateIndex = ${index};
                changingExpIds = [${stateList[index].transformation.map((t) => {
                    if (/^\d+$/.test(t.compId)) {
                        return t.compId;
                    }
                })}];
            `)

                for (const compItem of stateList[index].transformation) {

                    if (compItem.attribute === 'Hidden' && compItem.compId !== '') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: ${compItem.value}})`);
                    }

                    if (compItem.attribute === 'Select' && compItem.compId !== '') {
                        if (compItem.value === 'true') {
                            code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 1, lineWidth: 6})`);
                        } else if (compItem.value === 'false') {
                            code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 0.5, lineWidth: 2.5})`);
                        } else if (compItem.value === 'default') {
                            code.push(`window.calculator.setExpression({id:"${compItem.compId }", lineOpacity: 0.9, lineWidth: 2.5})`);
                        }
                    }

                    if (compItem.attribute === 'Color' && compItem.compId !== '') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: window.calculator.colors.${compItem.value}})`);
                    }

                    if (compItem.attribute === 'Custom' && compItem.compId !== '') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${compItem.customAttr }: ${compItem.customTrans }})`);
                    }

                    if (compItem.attribute === 'showLabel' && compItem.compId !== '') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", ${compItem.attribute}: ${compItem.value }})`);
                    }

                    if (compItem.attribute === 'pointActive' && compItem.compId !== '') {
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", showLabel: ${compItem.value }})`);
                        code.push(`window.calculator.setExpression({id:"${compItem.compId }", hidden: ${compItem.value === 'true' ? 'false': 'true'}})`);
                    }

                    if (compItem.attribute === 'Math Boundary') {
                        code.push(`window.calculator.setMathBounds({
                            left: ${+compItem.left},
                            right: ${+compItem.right},
                            bottom: ${+compItem.bottom},
                            top: ${+compItem.top}
                        })`);
                    }

                    if (compItem.attribute === 'xAxisLabel') {
                        code.push(`window.calculator.updateSettings({
                            xAxisLabel: "${compItem.xAxisLabel}",
                        })`);
                    }

                    if (compItem.attribute === 'yAxisLabel') {
                        code.push(`window.calculator.updateSettings({
                            yAxisLabel: "${compItem.yAxisLabel}",
                        })`);
                    }

                }
            code.push('}');
        }

        // set hidden if components has setted in above enter or exist state
        code.push(` else if (activeStateIndex < ${index} && (frameTop${setTopIndex(item.startFrame, frameIdStartOption) } > viewBottom)) {`);

        for (const compItem of stateList[index].transformation) {
            if (firstState && firstState.expressions && firstState.expressions.list[0]) {
                const changedExpressions = firstState.expressions.list.filter((sexp) => {
                    return sexp.id === compItem.compId;
                })
                if (changedExpressions && changedExpressions[0]) {
                    const cexp = changedExpressions[0]
                        if (compItem.attribute === "Hidden") {
                            const value = ({
                                true: false,
                                false: true,
                            })[compItem.value];
                            cexp.hidden = value;
                        }
                        if (compItem.attribute === "showLabel") {
                            const value = ({
                                true: false,
                                false: true,
                            })[compItem.value];
                            cexp.showLabel = value;
                        }
                        if (compItem.attribute === "pointActive") {
                            const value = ({
                                true: false,
                                false: true,
                            })[compItem.value];
                            cexp.showLabel = value;
                            cexp.hidden = !value;
                        }
                        if (compItem.attribute === "Select") {
                            const value = { lineOpacity: cexp.lineOpacity ? cexp.lineOpacity : 0.9, lineWidth: cexp.lineWidth ? cexp.lineWidth: 2.5 }
                            cexp.lineOpacity = value.lineOpacity;
                            cexp.lineWidth = value.lineWidth;
                        }
                        if (compItem.attribute === "Custom") {
                            const value = ({
                                true: false,
                                false: true,
                            })[compItem.customTrans];
                            cexp[compItem.customAttr] = value;
                        }

                        let expExistsInAboveStates = false;
                        for(const [idx, state] of stateList.entries()) {
                            if (idx < index) {
                                for (const trans of state.transformation) {
                                    if (trans.compId === compItem.compId && /^\d+$/.test(trans.compId)) {
                                        expExistsInAboveStates = true;
                                    }
                                }
                            }
                        }
                        code.push(`
                        if (!changingExpIds.includes(${compItem.compId}) && !${expExistsInAboveStates}) {
                            window.calculator.setExpression(${JSON.stringify(cexp)})
                        }`);
                        if (compItem.attribute === 'xAxisLabel') {
                            code.push(`window.calculator.updateSettings({
                                xAxisLabel: "${firstState.graph.xAxisLabel}",
                            })`);
                        }
                        if (compItem.attribute === 'yAxisLabel') {
                            code.push(`window.calculator.updateSettings({
                                yAxisLabel: "${firstState.graph.yAxisLabel}",
                            })`);
                        }
                        if (compItem.attribute === 'Math Boundary') {
                            code.push(`window.calculator.setMathBounds({
                                left: ${firstState.graph.viewport.xmin},
                                right: ${firstState.graph.viewport.xmax},
                                bottom: ${firstState.graph.viewport.ymin},
                                top: ${firstState.graph.viewport.ymax}
                            })`);
                        }
                }
            }
        }
        code.push('}');
 
    }

    // last curly brace of function 
    code.push('}}');

    // if user want to copy
    if (colors) {
        code.shift()
        code.unshift(`
        var state = ${JSON.stringify(firstState)};
        window.calculator.setState(state)`);
        code.unshift(`
        var element = document.getElementById('calculator${calculatorId}');
        var calculator${calculatorId} = Desmos.GraphingCalculator(element, {
            colors: ${JSON.stringify(colors)},
            border: false,
            invertedColors: true,
            showGrid: false,
            projectorMode: true,
            keypad: true,
            expressionsCollapsed: true,
            settingsMenu: true,
            lockViewport: false,
            fontSize: 16,
            expressions: true,
        });`);

        for(const i in code) {
            code[i] = code[i].replaceAll('window.calculator', `calculator${calculatorId}`);
        }

        code.push(`
            window.addEventListener('scroll', function () {
                window.checkIfInView()
            });
            window.addEventListener('resize', function () {
                window.scrollTo(0, 0);
                // rerender whole script
                var tags = document.getElementsByTagName("script");
                for (var i = tags.length; i >= 0; i--) {
                    if (
                        tags[i] &&
                        tags[i].getAttribute("src") != null &&
                        tags[i].getAttribute("src").slice(0, 23) === "data:text/plain;base64,"
                    ) {
                        // save src of script
                        var src = tags[i].src;
                        // remove it
                        tags[i].parentNode.removeChild(tags[i]);
                        // recreate it
                        var script = document.createElement("script");
                        script.onload = script.onerror = function () { this.remove(); };
                        script.setAttribute("src", src);
                        document.body.appendChild(script);
                    }
                }
            });
        `);
    }

    return code.join('\r\n')

}

export default engine;
