<template>
  <div class="leftengine large-blur">
    <div class="wrapper is--search">
      <div class="search is--template-border w-form">
        <input
          class="search_input w-input"
          autofocus
          v-model="frameUrl"
          placeholder="Frame URL"
          maxlength="256"
          dark
        />
        <input type="submit" value="" class="search_button-3 w-button" />
      </div>
      <a
        class="icon-btn is--template-border w-inline-block"
        @click="getFramesId()"
      >
        <img
          src="../images/refresh-button.svg"
          loading="lazy"
          alt=""
          class="icon-img is--large"
        />
      </a>
    </div>

    <div class="create-state-w">
      <h2 class="is--center">
        All States
        <span style="font-size: 1.2rem;" v-if="nameID">({{ nameID }})</span>
      </h2>
      <div class="little-row">
        <a
          class="icon-btn is--template-border"
          :style="codeIsRunning ? 'border-color: #2196F3;' : null"
          @click="onClickToRunCode()"
        >
          <i
            v-if="codeIsRunning"
            class="icon-img is--large fa fa-eye"
            style="color: #2196f3; font-size: large"
          ></i>
          <i v-if="!codeIsRunning" class="icon-img is--large fa fa-eye"></i>
        </a>
        <a
          class="icon-btn is--template-border"
          @click="onSendCodeToWebflow()"
        >
          <i class="fa fa-upload"></i>
        </a>
      </div>
    </div>

    <div class="allstates-w is--template-border">
      <div
        :class="codeIsRunning ? 'is--deselect': ''"
        :style="codeIsRunning ? 'z-index: -50;': ''"
        class="create-state-w is--template-border"
      >
        <u-search-select
          :options="
            frameIdStartOption.map((op) => {
              return { id: op, name: op };
            })
          "
          v-on:selected="
            (value) => {
              startFrame = value.name;
            }
          "
          :selectedOption="
            clickedFrame ? { id: clickedFrame, name: clickedFrame } : null
          "
          :disabled="false"
          :maxItem="10"
          placeholder="Select or search a frame"
        ></u-search-select>
        <a
          class="button w-button"
          :class="activeBtn === false ? 'is--deselect' : null"
          no-caps
          outline
          size="md"
          v-on:click="addNewState()"
        >
          Create State
        </a>
      </div>

      <div
        :class="codeIsRunning ? 'is--deselect': ''"
        :style="codeIsRunning ? 'z-index: -50;': ''"
      >
        <div
          v-for="(sitem, index) in stateList"
          :key="index"
          style="margin-bottom: 0.3rem; z-index: 1;"
          class="state-w is--template-border"
          :class="
            activeStateId === sitem.startFrame
              ? 'is--active'
              : null
          "
        >
          <div class="state_when-w">
            <div
              v-if="activeStateId === sitem.startFrame"
              class="active-label"
            >
              <div class="active_point"></div>
              <div>ACTIVE</div>
            </div>
            <div>{{ sitem.type }} State from</div>
            <u-search-select
              :options="
                frameIdStartOption.map((op) => {
                  return { id: op, name: op };
                })
              "
              v-on:selected="(value) => {
                sitem.startFrame = value.name;
              }"
              :selectedOption="{
                id: sitem.startFrame,
                name: sitem.startFrame,
              }"
              :disabled="false"
              :maxItem="10"
              placeholder="Select or search a frame"
            ></u-search-select>
            <div>to {{ sitem.endFrame }}</div>
            <a
              @click="onDuplicateState(sitem, index)"
              class="state_duplicate is--template-border w-inline-block"
            >
              <img
                src="../images/copy-2.svg"
                loading="lazy"
                alt=""
                class="icon-img is--large"
              />
            </a>
          </div>

          <div class="is--template-border">
            <div class="state_dropdown-trigger">
              <h4
                @click="
                  stateList[index].showDetails = !stateList[index].showDetails
                "
              >
                Click to View &quot;{{
                  sitem.transformation && sitem.transformation[0]
                    ? sitem.transformation.length
                    : 0
                }}&quot; Transformations
              </h4>
              <a
                :class="sitem.type === '' ? 'is--deselect' : null"
                class="state_actionbtn w-inline-block"
                @click="
                  addTransformation(index),
                    setBottomFrame(index, sitem.startFrame, sitem.type)
                "
              >
                <img
                  src="../images/add.svg"
                  loading="lazy"
                  alt=""
                  class="icon-img is--xlarge"
                />
              </a>
              <a
                :class="sitem.type === '' ? 'is--deselect' : null"
                class="state_actionbtn is--clear w-inline-block"
                @click="deleteTransformation(index)"
              >
                <img
                  src="../images/trash.svg"
                  loading="lazy"
                  alt=""
                  class="icon-img is--xlarge"
                />
              </a>
            </div>

            <div
              v-for="(item, element) in stateList[index].transformation"
              :key="element"
              class="transformation-w"
              style="margin-bottom: 0.3rem;"
            >
              <div
                v-show="stateList[index].showDetails"
                class="transformation-form-block w-form"
              >
                <div class="transformation-form-block w-form">
                  <div class="tranformation-form">
                    <div class="little-row2">
                      <a
                        v-if="selectingComponent &&
                          selectingComponent.stateIndex == index &&
                          selectingComponent.componentElement == element"
                        @click="selectingComponent = ''"
                        class="state_duplicate is--template-border w-inline-block"
                      >
                        <i class="fa fa-check" style="color: #00ffa1;"></i>
                      </a>
                      <select
                        class="dropdown w-dropdown-toggle"
                        style="max-width: 15rem;"
                        :class="
                          selectingComponent &&
                          selectingComponent.stateIndex == index &&
                          selectingComponent.componentElement == element
                            ? 'is--template-border'
                            : null
                        "
                        v-model="item.compId"
                        @focus="getCompId()"
                        @click="selectingComponent = ''"
                        @contextmenu="(e) => { e.preventDefault(), onSelectComponentId(index, element)}"
                        @change="
                          setAutoStateAndDetectAttribute(
                            item.compId,
                            index,
                            element
                          ),
                            (selectingComponent = '')
                        "
                      >
                        <option
                          disabled
                          class="dropdown-list w-dropdown-list w--open"
                          value=""
                        >
                          Component ID
                        </option>
                        <option
                          class="dropdown-list w-dropdown-list w--open"
                          v-for="(option, index) in componentIdOption"
                          :value="option.id"
                          :key="index"
                        >
                          <ul>
                            <li class="latex-render w-embed">
                              <label style="font-size: 2px;">{{ option.idx }}- </label>
                              <label>{{ option.latex }}</label>
                            </li>
                          </ul>
                        </option>
                        <option
                          class="dropdown-list w-dropdown-list w--open"
                          value="desmosCanvas"
                        >
                          Desmos Canvas
                        </option>
                      </select>
                      <!-- <u-search-select-math
                        :options="
                          componentIdOption.map((op) => {
                            return { id: op.id, name: op.latex };
                          })
                        "
                        v-on:selected="
                          (value) => {
                            item.compId = value.id;
                          }
                        "
                        :selectedOption="{
                          id: item.compId,
                          name: item.latex,
                        }"
                        :disabled="false"
                        :maxItem="10"
                        placeholder="Select or search a component"
                      ></u-search-select-math> -->
                    </div>

                    <select
                      class="dropdown w-dropdown-toggle"
                      :class="!item.compId ? 'is--deselect' : null"
                      outlined
                      v-model="item.attribute"
                      v-on:change="getAttr(item.attribute, index, element)"
                    >
                      <option
                        disabled
                        class="dropdown-list w-dropdown-list w--open"
                        value=""
                      >
                        Attribute
                      </option>
                      <option
                        class="dropdown-list w-dropdown-list w--open"
                        v-for="(option, i) in item.attributeOption"
                        :value="option"
                        :key="i"
                      >
                        <ul>
                          <li>
                            <label>{{ option }}</label>
                          </li>
                        </ul>
                      </option>
                    </select>
                    <div
                      class=""
                      v-if="
                        item.attribute === 'Color' ||
                        item.attribute === 'Hidden' ||
                        item.attribute === 'Select' ||
                        item.attribute === 'showLabel' ||
                        item.attribute === 'pointActive'
                      "
                    >
                      <select
                        class="dropdown w-dropdown-toggle"
                        :class="!item.attribute ? 'is--deselect' : null"
                        v-model="item.value"
                      >
                        <option
                          class="dropdown-list w-dropdown-list w--open"
                          value=""
                        >
                          Transform Option
                        </option>
                        <option
                          class="dropdown-list w-dropdown-list w--open"
                          v-for="(option, i) in stateList[index].transformation[
                            element
                          ].transformOption"
                          :value="option"
                          :key="i"
                        >
                          <ul>
                            <li>
                              <label>{{ option }}</label>
                            </li>
                          </ul>
                        </option>
                      </select>
                    </div>
                    <div class="" v-else-if="item.attribute === 'SYNC' || item.attribute === 'SYNC-Backward'">
                      <label for="latex">Latex</label>
                      <input
                        v-model="item.latex"
                        class="dropdown"
                        readonly
                      />
                      <label for="minValue">Slider Min</label>
                      <input
                        v-model="item.sliderBounds.min"
                        class="dropdown"
                        readonly
                      />
                      <label for="maxValue">Slider Max</label>
                      <input
                        v-model="item.sliderBounds.max"
                        class="dropdown"
                        readonly
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'SYNC-Custom'">
                      <label for="latex">Latex</label>
                      <input
                        v-model="item.latex"
                        class="dropdown"
                      />
                      <label for="minValue">Slider Min</label>
                      <input
                        v-model="item.sliderBounds.min"
                        class="dropdown"
                      />
                      <label for="maxValue">Slider Max</label>
                      <input
                        v-model="item.sliderBounds.max"
                        class="dropdown"
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'SYNC-Constant'">
                      <label for="latex">Latex</label>
                      <input
                        v-model="item.latex"
                        class="dropdown"
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'Custom'">
                      <label for="customAttr">Attribute</label>
                      <input
                        v-model="item.customAttr"
                        class="dropdown"
                        type="text"
                      />
                      <label for="customTrans">Value</label>
                      <input
                        v-model="item.customTrans"
                        class="dropdown"
                        type="text"
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'Math Boundary'">
                      <label for="left">left</label>
                      <input
                        v-model="item.left"
                        class="dropdown"
                        type="text"
                      />
                      <label for="right">right</label>
                      <input
                        v-model="item.right"
                        class="dropdown"
                        type="text"
                      />
                      <label for="bottom">bottom</label>
                      <input
                        v-model="item.bottom"
                        class="dropdown"
                        type="text"
                      />
                      <label for="top">top</label>
                      <input
                        v-model="item.top"
                        class="dropdown"
                        type="text"
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'xAxisLabel'">
                      <input
                        v-model="item.xAxisLabel"
                        class="dropdown"
                        type="text"
                      />
                    </div>
                    <div class="" v-else-if="item.attribute === 'yAxisLabel'">
                      <input
                        v-model="item.yAxisLabel"
                        class="dropdown"
                        type="text"
                      />
                    </div>
                    <a
                      @click="onDuplicateTransformation(item, element, index)"
                      class="state_duplicate is--template-border w-inline-block"
                    >
                      <img
                        src="../images/copy-2.svg"
                        loading="lazy"
                        alt=""
                        class="icon-img is--large"
                      />
                    </a>
                    <button
                      class="state_actionbtn is--clear"
                      @click="deleteComp(element, index)"
                    >
                      <img
                        src="../images/trash.svg"
                        loading="lazy"
                        alt=""
                        class="icon-img is--xlarge"
                      />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <flomotion-comp
      v-if="codeIsRunning"
      v-on:set-active-state="(stateId) => activeStateId = stateId"
      v-on:on-set-again-flomotion-component="onClickToRunCode(true)"
    ></flomotion-comp>

  </div>
</template>
<script>
// import authHeader from '@/middleware/authHeader'
import engine from "./flomotion";

export default {
  props: {
    clickedFrame: {
      type: String,
    },
  },
  data() {
    return {
      startFrame: "",
      endFrame: "",
      frameContent: "",
      frameUrl: "",
      stateMode: "",
      FrameTop: "",
      FrameBottom: "",
      frameIdStartOption: [],
      componentIdOption: [],
      stateList: [],
      lastDesmosList: [],
      activeBtnstatus: false,
      preElements: "",
      selectedState: "",
      selectingComponent: "",
      codeIsRunning: false,
      firstState: {},
      activeStateId: "",
      nameID: '',
      calculatorId: ''
    };
  },
  components: {
    uSearchSelect: () => import("../components/u-search-select.vue"),
    FlomotionComp: () => import("../components/flomotion-comp.vue"),
    // uSearchSelectMath: () => import("../components/u-search-select-math.vue"),
  },
  mounted () {
    this.setStatesFromLocalStorage()
  },
  computed: {
    activeBtn() {
      return this.activeBtnstatus;
    },
  },
  watch: {
    stateList: {
      handler: function () {
        this.onUpdateStateList();
      },
      deep: true,
    },
  },
  methods: {
    async getFramesId() {
      const loader = this.$loading.show();
      const allFrameIds = [];
      const url = new URL(this.frameUrl)
      const response = await this.$axios({ url: url.pathname, baseURL: url.origin })
      const div = document.createElement('div');
      div.insertAdjacentHTML('afterbegin', response.data );
      const lobj = div.getElementsByClassName("lobj-contet-section")
      //get all the frames inside of a given learning objective
      const allFrames = lobj[0].querySelector(':scope > .grid-frame-w').children
      for (let i=0; i<allFrames.length; i++) {
        //get the frame ID based on frame title. No > so it searches all the descendants
        const frameID = allFrames[i].querySelector(':scope  .frame_heading, .frame_heading-2').textContent
          .replace(/ /g, "-").replace(/^[^a-z]+|[^\w:.-]+/gi, "");
        allFrames[i].id = frameID;
      }

      allFrames.forEach((frame) => {
        if (frame.id) {
          allFrameIds.push(frame.id)
        }
      });
      if (allFrameIds && allFrameIds[0]) {
        this.activeBtnstatus = true
      }
      this.frameIdStartOption = allFrameIds;
      this.$emit('frameId', this.frameIdStartOption)
      this.getCompId();
      loader.hide()
    },
    addNewState() {
      this.stateList.filter((state) => {
        return state.startFrame !== "";
      });
      this.stateList.push({
        startFrame: this.startFrame,
        endFrame: "",
        type: "enter",
        transformation: [],
        showDetails: false,
      });
      this.stateList = this.stateList.sort((a, b) => {
        if (this.frameIdStartOption.indexOf(a.startFrame) < this.frameIdStartOption.indexOf(b.startFrame)) {
          return -1
        } else if (this.frameIdStartOption.indexOf(a.startFrame) > this.frameIdStartOption.indexOf(b.startFrame)) {
          return 1
        }
        return 0
      })
    },
    deleteComp(element, index) {
      this.stateList[index].transformation.splice(element, 1);
      if (element.compId !== "" || element.compId !== undefined) {
        this.stateList[index].transformation = this.stateList[index].transformation.filter(
          (scitem) => {
            return scitem.compId !== "";
          }
        );
      }
    },
    addTransformation(index) {
      this.stateList[index].showDetails = true;
      this.stateList[index].transformation.push({
        compId: "",
        attribute: "",
        value: "",
        latex: "",
        attributeOption: [],
        transformOption: [],
        sliderBounds: {},
        customAttr: "",
        customTrans: "",
      });
    },
    deleteTransformation(index) {
      this.stateList.splice(index, 1);
    },
    getCompId() {
      const expressions = window.calculator.getExpressions();
      this.lastDesmosList[0] = expressions;
      expressions.forEach((exp, idx) => {
        exp.latex = exp.latex ? exp.latex.slice(0, 50) : '';
        if (exp.latex && this.componentIdOption.findIndex((cio) => cio.id === exp.id) < 0) {
          this.componentIdOption.push({ ...exp, idx: idx+1 });
        }
      });
      window.MathJax.typeset();
    },
    setBottomFrame(stateIndex, startId, type) {
      if (type === "inView") {
        this.stateList[stateIndex].endFrame = startId;
      }
      if (type === "enter") {
        this.stateList.forEach((stateItem, index) => {
          if (stateItem && index > stateIndex) {
            stateItem
              ? (this.stateList[stateIndex].endFrame =
                  stateItem.startFrame)
              : (this.stateList[stateIndex].endFrame = null);
          }
        });
      }
    },
    setAutoStateAndDetectAttribute(id, index, element) {
      const compIndex = this.stateList[index].transformation.findIndex((item, i) => {
        return item.compId === id && i === element;
      });
      // reset all part of a transformation
      this.stateList[index].transformation[compIndex].attribute = "";
      this.stateList[index].transformation[compIndex].latex = "";
      this.stateList[index].transformation[compIndex].sliderBounds = {};
      this.stateList[index].transformation[compIndex].attributeOption = [
        "Hidden",
        "Color",
        "Select",
        "Custom",
      ];

      // detect desmos canvas
      if (id === "desmosCanvas") {
        this.stateList[index].transformation[compIndex].attribute = "Math Boundary";
        this.stateList[index].transformation[compIndex].attributeOption = [
          "Math Boundary", "xAxisLabel", "yAxisLabel"
        ];
      }
      // auto change latex
      this.lastDesmosList[0].forEach((item) => {
        if (item.type === "expression" && item.id === id) {
          this.stateList[index].transformation[compIndex].latex = item.latex;
          const ilatex = item.latex.split("=").pop();
          if (item.domain && !isNaN(ilatex)) {
            this.stateList[index].transformation[compIndex].attribute = "SYNC";
            this.stateList[index].transformation[compIndex].attributeOption = [
              "SYNC", "SYNC-Custom", "SYNC-Backward", "SYNC-Constant"
            ];
            this.stateList[index].transformation[compIndex].sliderBounds.min =
              item.sliderBounds ? item.sliderBounds.min : 0;
            this.stateList[index].transformation[compIndex].sliderBounds.max =
              item.sliderBounds ? item.sliderBounds.max : +ilatex;
          }
          const lLatex = item.latex.slice(1, 6);
          if (lLatex === "left(") {
            this.stateList[index].transformation[compIndex].attributeOption = [
              "Hidden",
              "Color",
              "Select",
              "Custom",
              "showLabel",
              "pointActive"
            ];
          }
        }
      });
    },
    getAttr(option, index, element) {
      this.stateList[index].transformation[element].transformOption = [];
      if (option && option === "Color") {
        this.stateList[index].transformation[element].transformOption = [
          "green",
          "blue",
          "red",
          "pink",
          "yellow",
          "purple",
          "guideline",
          "white",
        ];
      } else if (option && option === "Hidden") {
        this.stateList[index].transformation[element].transformOption = [
          "true",
          "false",
        ];
      } else if (option && option === "Select") {
        this.stateList[index].transformation[element].transformOption = [
          "true",
          "false",
          "default",
        ];
      } else if (option && (option === "showLabel" || option === "pointActive")) {
        this.stateList[index].transformation[element].transformOption = [
          "true",
          "false",
        ];
      } else {
        return "";
      }
    },
    onUpdateStateList() {
      // auto change item state
      this.stateList.forEach((state) => {
        const sliderIndex = state.transformation.findIndex((cp) => {
          const ilatex = cp.latex.split("=").pop();
          return ilatex && !isNaN(ilatex);
        });
        // auto change item state
        if (sliderIndex >= 0) {
          state.type = "inView";
        } else {
          state.type = "enter";
        }
      });
    },
    onClickToRunCode(resized) {
      this.codeIsRunning = !this.codeIsRunning;
      this.onExecuteStateCode();
      if (resized) {
        this.onClickToRunCode()
      }
    },
    onExecuteStateCode() {
      if (this.codeIsRunning) {
        // auto sort bug
        this.stateList = this.stateList.sort((a, b) => {
          if (this.frameIdStartOption.indexOf(a.startFrame) < this.frameIdStartOption.indexOf(b.startFrame)) {
            return -1
          } else if (this.frameIdStartOption.indexOf(a.startFrame) > this.frameIdStartOption.indexOf(b.startFrame)) {
            return 1
          }
          return 0
        });
        window.scrollTo(0, 0);
        this.onCreateFirstState();
        const code = engine(
          this.stateList,
          this.frameIdStartOption,
          this.firstState
        );
        this.executeScript(code);
      } else {
        window.calculator.setState(this.firstState);
        window.activeStateId = '';
        this.activeStateId = window.activeStateId;
        const tags = document.getElementsByTagName("script");
        for (let i = tags.length; i >= 0; i--) {
          if (
            tags[i] &&
            tags[i].getAttribute("src") != null &&
            tags[i].getAttribute("src").slice(0, 23) === "data:text/plain;base64,"
          ) {
            tags[i].parentNode.removeChild(tags[i]);
          }
        }
      }
    },
    onCreateFirstState() {
      const state = window.calculator.getState();
      state.expressions.list.forEach((ste) => {
        ste.hidden = true;
        if (ste.latex && ste.latex.slice(1, 6) === "left(") {
          ste.showLabel = false;
        }
      })
      this.firstState = state;
    },
    executeScript(source) {
      const script = document.createElement("script");
      script.onload = script.onerror = function () { this.remove(); };
      script.setAttribute("src", "data:text/plain;base64," + btoa(unescape(encodeURIComponent(source))));
      document.body.appendChild(script);
    },
    copyToClipboard(text) {
      var dummy = document.createElement("textarea");
      document.body.appendChild(dummy);
      dummy.value = text;
      dummy.select();
      document.execCommand("copy");
      document.body.removeChild(dummy);
    },
    onDuplicateTransformation(transformation, element, index) {
      this.stateList[index].transformation.push({
        compId: transformation.compId,
        attribute: transformation.attribute,
        value: transformation.value,
        latex: transformation.latex,
        attributeOption: transformation.attributeOption,
        transformOption: transformation.transformOption,
        sliderBounds: transformation.sliderBounds,
        customAttr: transformation.customAttr,
        customTrans: transformation.customTrans,
      });
    },
    onDuplicateState(state) {
      this.stateList.push({
        startFrame: state.startFrame,
        endFrame: state.endFrame,
        type: state.type,
        transformation: [],
        showDetails: state.showDetails,
      });
      state.transformation.forEach((st) => {
        this.stateList[this.stateList.length-1].transformation.push({
          compId: st.compId,
          attribute: st.attribute,
          value: st.value,
          latex: st.latex,
          attributeOption: st.attributeOption,
          transformOption: st.transformOption,
          sliderBounds: st.sliderBounds,
          customAttr: st.customAttr,
          customTrans: st.customTrans,
        });
      });
      this.stateList = this.stateList.sort((a, b) => {
        if (this.frameIdStartOption.indexOf(a.startFrame) < this.frameIdStartOption.indexOf(b.startFrame)) {
          return -1
        } else if (this.frameIdStartOption.indexOf(a.startFrame) > this.frameIdStartOption.indexOf(b.startFrame)) {
          return 1
        }
        return 0
      })
    },
    onSelectComponentId(index, element) {
      this.selectingComponent = {
        stateIndex: index,
        componentElement: element,
      };
    },
    async onSendCodeToWebflow () {
      const loader = this.$loading.show();
      this.onCreateFirstState();
      this.calculatorId = prompt("please insert an id to desmos", this.calculatorId)
      const colors = window.calculator.colors;
      const copy = engine(
        this.stateList,
        this.frameIdStartOption,
        this.firstState,
        colors,
        this.calculatorId
      );
      const updateUrl = this.$route.query.id ? `/${this.$route.query.id}`: '';
      const routeNameID = this.$route.query.nameID ? this.$route.query.nameID : '';
      const nameID = prompt("Please insert your state name", routeNameID.replace(/ /g, "-").replace(/^[^a-z]+|[^\w:.-]+/gi, ""));
      if (nameID != null) {
        this.nameID = nameID.replace(/ /g, "-").replace(/^[^a-z]+|[^\w:.-]+/gi, "");
      }
      const newUrl = new URL(`https://x8ki-letl-twmt.n7.xano.io/api:rE9e_rW1/flomotion${updateUrl}`)
      const response = await this.$axios({ method: "post",  url: newUrl.pathname, baseURL: newUrl.origin, data: {
          desmos: this.firstState,
          jsCode: btoa(unescape(encodeURIComponent(copy))),
          desmosID: window.desmosUrl,
          frameID: this.frameUrl,
          states: this.stateList,
          nameID: this.nameID,
        }
      });
      if (response.data.id) {
        prompt("code and states has uploaded with id: ", response.data.id)
      } else {
        alert("something is wrong!")
      }
      // const response = await this.$axios.post("/flomotion/add", {
      //   flomotiondesmos: this.firstState,
      //   animation: btoa(unescape(encodeURIComponent(copy))),
      // });
      // if (response.data.data._id) {
      //   prompt("code and states has uploaded with id: ", response.data.data._id)
      // } else {
      //   alert("something is wrong!")
      // }
      loader.hide();
    },
    setComponentFromDesmos(e) {
      const expressions = window.calculator.getExpressions();

      // set expression id for selects
      let id = window.calculator.selectedExpressionId;
      if (typeof this.selectingComponent === "object" && id) {
        const folderIndex = expressions.findIndex(exp => exp.id === id && exp.type === "folder");
        // select first exp after folder
        folderIndex > -1 ? id = expressions[folderIndex + 1].id: '';
        if (e && !e.altKey) {
          this.stateList[this.selectingComponent.stateIndex].transformation[
            this.selectingComponent.componentElement
          ].compId !== id ||
          !this.stateList[this.selectingComponent.stateIndex].transformation[
            this.selectingComponent.componentElement
          ].compId
            ? (this.stateList[this.selectingComponent.stateIndex].transformation[
                this.selectingComponent.componentElement
              ].compId = id)
            : null;
          this.setAutoStateAndDetectAttribute(
            id,
            this.selectingComponent.stateIndex,
            this.selectingComponent.componentElement
          );
          this.selectingComponent = "";
        } else if (e && e.altKey) {
          if (!this.stateList[this.selectingComponent.stateIndex].transformation[
            this.selectingComponent.componentElement
          ].compId) {
            this.stateList[this.selectingComponent.stateIndex].transformation[
                this.selectingComponent.componentElement
              ].compId = id
            this.setAutoStateAndDetectAttribute(
              id,
              this.selectingComponent.stateIndex,
              this.selectingComponent.componentElement
            );
          }
          const transformation = this.stateList[this.selectingComponent.stateIndex].transformation[
            this.selectingComponent.componentElement
          ]
          this.stateList[this.selectingComponent.stateIndex].transformation.splice(
            this.selectingComponent.componentElement+1, 0, {
              compId: id,
              attribute: transformation.attribute,
              value: transformation.value,
              latex: transformation.latex,
              attributeOption: transformation.attributeOption,
              transformOption: transformation.transformOption,
              sliderBounds: transformation.sliderBounds,
              customAttr: transformation.customAttr,
              customTrans: transformation.customTrans,
            })
        }
      }

      // update same ids and detect no ids
      if (expressions && expressions[0]) {
        // live update stateList
        let slidersIndexes = [];
        this.stateList.forEach((state) => {
          slidersIndexes = state.transformation
            .map((cp, idx) => {
              const ilatex = cp && cp.latex ? cp.latex.split("=").pop(): '';
              return ilatex && !isNaN(ilatex) ? idx : "";
            })
            .filter(String);
          for (const i in slidersIndexes) {
            expressions.forEach((exp) => {
              if(exp && exp.latex) {
                const ilatex = exp.latex.split("=").pop();
                if (
                  ilatex &&
                  !isNaN(ilatex) &&
                  state.transformation[slidersIndexes[i]].compId === exp.id &&
                  state.transformation[slidersIndexes[i]].sliderBounds &&
                  state.transformation[slidersIndexes[i]].attribute === "SYNC"
                ) {
                  state.transformation[slidersIndexes[i]].sliderBounds =
                    exp.sliderBounds || { min: 0, max: 15, step: 1 }
                }
              }
            });
          }
        });
      }
    },
    async setStatesFromLocalStorage () {
      this.frameUrl = this.$route.query.lobjID;
      window.desmosUrl = this.$route.query.canvasID;
      this.nameID = this.$route.query.nameID ? this.$route.query.nameID: '';
      if (this.$route.query.id) {
        if (this.frameUrl) {
          await this.getFramesId();
        }
        const newUrl = new URL(`https://x8ki-letl-twmt.n7.xano.io/api:rE9e_rW1/flomotion/${this.$route.query.id}`)
        const response = await this.$axios({ method: "get",  url: newUrl.pathname, baseURL: newUrl.origin})
        const currentStates = response.data.states;
        const json = JSON.stringify(response.data.desmos);
        if (json) {
          const stateZero = JSON.parse(json, (key, val) => (
            key !== 'version' && key !== 'xAxisStep' && key !== 'xAxisMinorSubdivisions' && key !== 'yAxisMinorSubdivisions' &&
            typeof val !== 'object' && val !== null ? String(val) : val
          ));
          await window.calculator.setState(stateZero);
        }
        if (currentStates && currentStates[0]) {
          await currentStates.forEach((cs) => {
            cs.showDetails = true;
            cs.transformation.forEach((ct) => {
              ct.attributeOption = ["Hidden", "Color", "Select", "Custom"]
              if (ct.compId === "desmosCanvas") {
                ct.attributeOption = ["Math Boundary", "xAxisLabel", "yAxisLabel"];
              } else if (ct.attribute === "SYNC" || ct.attribute === "SYNC-Custom") {
                !ct.sliderBounds && !ct.slider ? ct.sliderBounds = {
                  min: 0, max: 15, step: 1
                }: '';
                ct.attributeOption = ["SYNC", "SYNC-Custom", "SYNC-Backward", "SYNC-Constant"];
              } else if (ct.attribute === "showLabel" || ct.attribute === "pointActive") {
                ct.attributeOption = ["Hidden", "Color", "Select", "Custom", "showLabel", "pointActive"];
              }
            })
          });
          this.stateList = currentStates;
        }
      }
    },
  },
};
</script>

