<template>
  <div class="section-w">
    <div
      class="animation-engine-w"
      :style="flomotionShowing ? '':'grid-template-columns: auto'"
    >
      <CreateCode
        v-show="flomotionShowing"
        @frameId="frameId"
        :clickedFrame="clickedFrame"
        ref="createCode"
      />

      <div class="wrapper is--blurcolor large-blur">
        <div class="rightengine">
          <ScrollingFrames
            :frameIds="frameIds"
            v-on:clicked-on-frame="clickedOnFrame"
            v-on:dbclicked-on-frame="dbclickedOnFrame"
            v-on:on-toggle-flomotion="flomotionShowing = ! flomotionShowing"
            ref="scrollingComp"
          />

          <div class="fullheightline is--thin"></div>

          <DisplayCode
            :stateList="stateList"
            v-on:on-click-to-desmos="onClickToDesmos"
            ref="displayComp"
          />
        </div>
      </div>
    </div>
    <div class="wallpaper"></div>
  </div>
</template>
<script>
import CreateCode from '../components/craete-code-left'
import ScrollingFrames from '../components/scrolling-frames'
import DisplayCode from '../components/display-code-right'

export default {
  name: "Index",
  data () {
    return {
      frameIds: [],
      stateList: '',
      clickedFrame: '',
      state: '',
      flomotionShowing: true,
    }
  },
  components: {
    CreateCode,
    ScrollingFrames,
    DisplayCode
  },
  mounted () {
  },
  methods: {
    frameId (event) {
      this.frameIds = event
    },
    clickedOnFrame (item) {
      this.clickedFrame = item;
    },
    dbclickedOnFrame (item) {
      this.clickedFrame = item;
      this.$refs.createCode.addNewState();
    },
    onClickToDesmos (e) {
      this.$refs.createCode.setComponentFromDesmos(e)
    },
    shortcutFuncs (event) {
        if (event.key === 'E') {
            this.$refs.createCode.onClickToRunCode()
        } else if (event.key === 'S') {
            this.$refs.createCode.onSendCodeToWebflow()
        } else if (event.key === 'D') {
            this.flomotionShowing = ! this.flomotionShowing
        }
    },
  },
  created() {
      window.addEventListener("keyup", this.shortcutFuncs);
  },
  destroyed() {
      window.removeEventListener("keyup", this.shortcutFuncs);
  },
}
</script>

<style>
.blur {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  }
.large-blur {
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  }
</style>