<template>
    <div class="framerep-w">
      <div class="viewtop large-blur">
        <div class="heading-24">View Top</div>
      </div>
      <div class="vertical-center">
        <button
          type="button"
          class="icon-btn is--template-border w-inline-block"
          @click="onToggleFlomotion"
          style="background-color: #3a3a3a;"
        >
          <i v-if="flomotionShowing" class="fa fa-angle-left"></i>
          <i v-if="!flomotionShowing" class="fa fa-angle-right"></i>
        </button>
      </div>
      <div class="viewbottom large-blur">
        <div class="heading-24">View Bottom</div>
      </div>
      <div v-if="frameIds && frameIds[0]">
        <div id="jack-production" class="framerep is--template-border">
          <div class="framerep-text">SCROLL ME !</div>
        </div>
        <div v-for ="(item, index) in frameIds" :key="index">
          <div
            :id="item"
            @click="$emit('clicked-on-frame', item)"
            v-on:dblclick="$emit('dbclicked-on-frame', item)"
            class="framerep is--template-border"
          >
            <div class="framerep-text">{{ item }}</div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      viewBottom: '',
      viewTop: '',
      flomotionShowing: true,
    }
  },
  props: {
    frameIds: {
      type: Array
    }
  },
  methods: {
    onToggleFlomotion () {
      this.$emit('on-toggle-flomotion');
      this.flomotionShowing = !this.flomotionShowing;
    }
  }
}
</script>
<style scoped>
.vertical-center {
  position: sticky;
  top: 50%;
  margin-left: -20px;
  width: 10px;
}
</style>
